import React, { useState } from "react";
import { blockExplore } from "./Web3";

const App = () => {
  const [buyPrice, setBuyPrice] = useState(0);
  const [feesPercent, setFeesPercent] = useState(0);
  const [gasCost, setGasCost] = useState(0);
  const [desiredProfit, setDesiredProfit] = useState(0);
  const [sellingPrice, setSellingPrice] = useState(0);
  const [ethPrice, setEthPrice] = useState(1600);
  const [localPrice, setlocalPrice] = useState(0);

  // Block Ex
  const [gasPrice, setGasPrice] = useState(0);
  const [blockNumber, setBlockNumber] = useState(0);

  const handleBuyPriceChange = (event) => {
    setBuyPrice(event.target.value);
  };

  const handleFeesPercentChange = (event) => {
    setFeesPercent(event.target.value);
  };

  const handleGasCostChange = (event) => {
    setGasCost(event.target.value);
  };

  const handleDesiredProfitChange = (event) => {
    setDesiredProfit(event.target.value);
  };

  const handleEthPriceChange = (event) => {
    setEthPrice(event.target.value);
  };

  const handleLocalPriceChange = (event) => {
    setlocalPrice(event.target.value);
  };

  React.useEffect(() => {
    setSellingPrice(
      (Number(buyPrice) + Number(desiredProfit) + Number(gasCost)) /
        (1 - Number(feesPercent) / 100)
    );

    const gasHandler = async () => {
      const { gasPrice, blockNumber } = await blockExplore();
      setGasPrice(gasPrice);
      setBlockNumber(blockNumber);
    };

    const intervalId = setInterval(gasHandler, 5000);
    gasHandler();

    return () => clearInterval(intervalId);
  }, [buyPrice, desiredProfit, feesPercent, gasCost]);

  return (
    <div style={styles.container}>
      <div style={{ marginTop: "20px" }}>
        <h2>
          <span role="img" aria-label="gasemo">
            🟢 💨
          </span>{" "}
          Gas Price: {Number(gasPrice).toFixed(2)}
        </h2>
        <h2>
          <span role="img" aria-label="gasemo">
            🟢
          </span>{" "}
          Lstest Block: {blockNumber}
        </h2>
      </div>
      <div
        style={{
          border: "1px solid gray",
          borderRadius: "3px",
          marginTop: "50px",
          padding: "25px"
        }}
      >
        <h1
          style={{
            //marginTop: "100px",
            backgroundColor: "blue",
            borderRadius: "3px",
            padding: "10px",
            textAlign: "center"
          }}
        >
          Flipper Calculator
        </h1>
        <div style={styles.inputContainer}>
          <div style={styles.inputLabel}>Buy Price:</div>
          <input
            type="number"
            value={buyPrice}
            onChange={handleBuyPriceChange}
            style={styles.input}
            placeholder="0"
          />
          <div style={styles.inputSuffix}>ETH</div>
        </div>

        <div style={styles.inputContainer}>
          <div style={styles.inputLabel}>ETH Price:</div>
          <input
            type="number"
            value={ethPrice}
            onChange={handleEthPriceChange}
            style={styles.input}
            placeholder="0"
          />
          <div style={styles.inputSuffix}>$</div>
        </div>

        <div style={styles.inputContainer}>
          <div style={styles.inputLabel}>Local Price:</div>
          <input
            type="number"
            value={localPrice}
            onChange={handleLocalPriceChange}
            style={styles.input}
            placeholder="0"
          />
          <div style={styles.inputSuffix}>T</div>
        </div>

        <div style={styles.inputContainer}>
          <div style={styles.inputLabel}>Market Fees + Royalty %:</div>
          <input
            type="number"
            value={feesPercent}
            onChange={handleFeesPercentChange}
            style={styles.input}
            placeholder="0"
          />
          <div style={styles.inputSuffix}>%</div>
        </div>
        <div style={styles.inputContainer}>
          <div style={styles.inputLabel}>Gas Costs:</div>
          <input
            type="number"
            value={gasCost}
            onChange={handleGasCostChange}
            style={styles.input}
            placeholder="0"
          />
          <div style={styles.inputSuffix}>ETH</div>
        </div>

        <div
          style={{
            display: "flex",
            alignItems: "center",
            margin: 10,
            marginBottom: "50px",
            borderBottom: "1px solid gray",
            padding: "20px"
          }}
        >
          <div style={styles.inputLabel}>Desired Profit:</div>
          <input
            type="number"
            value={desiredProfit}
            onChange={handleDesiredProfitChange}
            style={styles.input}
            placeholder="0"
          />
          <div style={styles.inputSuffix}>ETH</div>
        </div>
        <div style={styles.result}>
          <p>Total Spendings:</p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "gray",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {(Number(buyPrice) + Number(gasCost)).toFixed(4)} ETH
          </p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "gray",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {((Number(buyPrice) + Number(gasCost)) * ethPrice).toLocaleString()}{" "}
            $
          </p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "gray",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {(
              (Number(buyPrice) + Number(gasCost)) *
              ethPrice *
              localPrice
            ).toLocaleString()}{" "}
            T
          </p>
        </div>
        <div style={styles.result}>
          <p>Sell Price:</p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "gold",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {sellingPrice.toFixed(4)} ETH
          </p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "gold",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {(sellingPrice * ethPrice).toLocaleString()} $
          </p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "gold",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {(sellingPrice * ethPrice * localPrice).toLocaleString()} T
          </p>
        </div>
        <div style={styles.result}>
          <p>Profit:</p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "green",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {desiredProfit} ETH
          </p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "green",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {(desiredProfit * ethPrice).toLocaleString()} $
          </p>
          <p
            style={{
              color: "black",
              fontWeight: "bold",
              backgroundColor: "green",
              borderRadius: "3px",
              padding: "15px"
            }}
          >
            {(desiredProfit * ethPrice * localPrice).toLocaleString()} T
          </p>
        </div>
      </div>
    </div>
  );
};

const styles = {
  container: {
    backgroundColor: "#1b1b1b",
    color: "whitesmoke",
    fontFamily: "monospace",
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  inputContainer: {
    display: "flex",
    alignItems: "center",
    margin: 10,
    marginBottom: "20px",
    justifyContent: "space-between",
    fontSize: "15px"
  },
  inputLabel: {
    width: 120,
    textAlign: "right",
    marginRight: 10
  },
  input: {
    width: 100,
    height: 30,
    padding: 6,
    fontSize: "15px",
    borderRadius: 3,
    border: "1px solid lightgray",
    backgroundColor: "#1b1b1b",
    color: "whitesmoke"
  },
  inputSuffix: {
    marginLeft: 10
  },
  result: {
    marginTop: 0,
    fontSize: 20,
    fontStyle: "bold",
    display: "grid",
    flexDirection: "cloumn",
    lineHeight: "0px",
    alignItems: "center",
    justifyContent: "space-around",
    textAlign: "center"
  }
};

export default App;
